import gql from 'graphql-tag'
import { FPromoCodeFragmentDoc } from '../../graph/generated'

export const OrderPromoCodesQuery = gql`
  query GOrderPromoCodes($orderId: ID!) {
    order(id: $orderId) {
      id
      promoCodes {
        ...FPromoCode
      }
    }
  }
  ${FPromoCodeFragmentDoc}
`

export const AddOrderPromoMutation = gql`
  mutation MAddOrderPromo($orderId: ID!, $code: String!) {
    addOrderPromoCode(order: $orderId, promoCode: $code) {
      userErrors: errors
      order {
        id
        promoCodes {
          ...FPromoCode
        }
      }
    }
  }
  ${FPromoCodeFragmentDoc}
`

export const RemoveOrderPromoMutation = gql`
  mutation MRemoveOrderPromo($orderId: ID!, $codeId: ID!) {
    removeOrderPromoCode(order: $orderId, promoCode: $codeId) {
      id
      promoCodes {
        ...FPromoCode
      }
    }
  }
  ${FPromoCodeFragmentDoc}
`
